@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Akaya+Kanadaka&display=swap);
@import url(https://fonts.googleapis.com/css?family=Frank+Ruhl+Libre:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --primary-color: #ffa500;
  /* --primary-color: blue;
  --primary-color: cyan; */
}

*{
  padding: 0;
  margin: 0;
}

body{
  /* font-family: 'Lato', sans-serif; */
  /* font-family: 'Akaya Kanadaka', cursive; */
  font-family: 'Frank+Ruhl+Libre', sans-serif;
  color: #343434;
  color: #212F3C;
  color: #1B2631;
  color: #283747;
}

p{
  /* font-size: 13px; */
  font-size: 12px;
  line-height: 18px;
}

.container{
  margin: 50px 50px;
}

.orange-text{
  color: #ffa500;
  color: var(--primary-color);
}

.section-header{
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: 10px 0;
}

.inline-block{
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: 10px 0;
}

.custom-icon{
  color: #ffa500;
  color: var(--primary-color);
  padding-right: 10px;
}

.name{
  letter-spacing: 3px;
  color: #ffa500;
  color: var(--primary-color);
}

h3{
  color: #ffa500;
  color: var(--primary-color);
}

.custom-list{
  margin-left: 5%;
}

.project{
  margin: 5px 0;
}

.project-desc{
  margin-left: 15px;;
}

.skill-level {
  height: 5px !important;
  border-radius: 3px !important;
  margin: 15px 0;
  background-color: grey !important;
}

.skill-level .MuiLinearProgress-bar{
  background-color: #ffa500 !important;
  background-color: var(--primary-color) !important;
}

.MuiSvgIcon-fontSizeSmall{
  font-size: 1rem !important;
}

